.tabs
  @extend %block
  +utilities.overflow-touch
  @extend %unselectable
  align-items: stretch
  display: flex
  font-size: var(--tabs-font-size)
  justify-content: space-between
  overflow: hidden
  overflow-x: auto
  white-space: nowrap
  a
    align-items: center
    border-bottom-color: var(--tabs-border-bottom-color)
    border-bottom-style: var(--tabs-border-bottom-style)
    border-bottom-width: var(--tabs-border-bottom-width)
    color: var(--tabs-link-color)
    display: flex
    justify-content: center
    +utilities.logical-y(margin, calc(-1 * var(--tabs-border-bottom-width)), $bottom: true)
    padding: var(--tabs-link-padding)
    vertical-align: top
    &:hover
      border-bottom-color: var(--tabs-link-hover-border-bottom-color)
      color: var(--tabs-link-hover-color)
  li
    display: block
    &.is-active
      a
        border-bottom-color: var(--tabs-link-active-border-bottom-color)
        color: var(--tabs-link-active-color)
  ul
    align-items: center
    border-bottom-color: var(--tabs-border-bottom-color)
    border-bottom-style: var(--tabs-border-bottom-style)
    border-bottom-width: var(--tabs-border-bottom-width)
    display: flex
    flex-grow: 1
    flex-shrink: 0
    justify-content: flex-start
    &:is(.is-left, .is-start)
      +utilities.logical-x(padding, 0.75em, $right: true)
    &.is-center
      flex: none
      justify-content: center
      +utilities.logical-x(padding, 0.75em)
    &:is(.is-right, .is-end)
      justify-content: flex-end
      +utilities.logical-x(padding, 0.75em, $right: false)
  .icon
    &:first-child
      +utilities.logical-x(margin, 0.5em, $right: true)
    &:last-child
      +utilities.logical-x(margin, 0.5em, $right: false)
  // Alignment
  &.is-centered
    ul
      justify-content: center
  &:is(.is-right, .is-end)
    ul
      justify-content: flex-end
  // Styles
  &.is-boxed
    a
      border: 1px solid transparent
      +utilities.logical-border-radius(top, var(--tabs-boxed-link-radius))
      +utilities.logical-border-radius(bottom, 0)
      &:hover
        background-color: var(--tabs-boxed-link-hover-background-color)
        border-bottom-color: var(--tabs-boxed-link-hover-border-bottom-color)
    li
      &.is-active
        a
          background-color: var(--tabs-boxed-link-active-background-color)
          border-color: var(--tabs-boxed-link-active-border-color)
          border-bottom-color: var(--tabs-boxed-link-active-border-bottom-color) !important
  &.is-fullwidth
    li
      flex-grow: 1
      flex-shrink: 0
  &.is-toggle
    a
      border-color: var(--tabs-toggle-link-border-color)
      border-style: var(--tabs-toggle-link-border-style)
      border-width: var(--tabs-toggle-link-border-width)
      +utilities.logical-y(margin, 0, $bottom: true)
      position: relative
      &:hover
        background-color: var(--tabs-toggle-link-hover-background-color)
        border-color: var(--tabs-toggle-link-hover-border-color)
        z-index: 2
    li
      & + li
        +utilities.logical-x(margin, calc(-1 * var(--tabs-toggle-link-border-width)), $right: false)
      &:first-child a
        +utilities.logical-border-radius(left, var(--tabs-toggle-link-radius))
      &:last-child a
        +utilities.logical-border-radius(right, var(--tabs-toggle-link-radius))
      &.is-active
        a
          background-color: var(--tabs-toggle-link-active-background-color)
          border-color: var(--tabs-toggle-link-active-border-color)
          color: var(--tabs-toggle-link-active-color)
          z-index: 1
    ul
      border-bottom: none
    &.is-toggle-rounded
      li
        &:first-child a
          +utilities.logical-x(padding, 1.25em, $right: false)
          +utilities.logical-border-radius(left, var(--radius-rounded))
        &:last-child a
          +utilities.logical-x(padding, 1.25em, $right: true)
          +utilities.logical-border-radius(right, var(--radius-rounded))
  // Sizes
  +utilities.control-sizes(tabs-font-size)
