@use "../utilities"

$title-color: var(--text-strong) !default
$title-family: false !default
$title-size: var(--size-3) !default
$title-weight: var(--weight-semibold) !default
$title-line-height: 1.125 !default
$title-strong-color: inherit !default
$title-strong-weight: inherit !default
$title-sub-size: 0.75em !default
$title-sup-size: 0.75em !default

$subtitle-color: var(--text) !default
$subtitle-family: false !default
$subtitle-size: var(--size-5) !default
$subtitle-weight: var(--weight-normal) !default
$subtitle-line-height: 1.25 !default
$subtitle-strong-color: var(--text-strong) !default
$subtitle-strong-weight: var(--weight-semibold) !default
$subtitle-negative-margin: -1.25rem !default

// Map Local variables
$-css-vars-map: ("title-sub-size": $title-sub-size, "title-family": $title-family, "title-sup-size": $title-sup-size, "title-color": $title-color, "title-size": $title-size, "title-weight": $title-weight, "title-line-height": $title-line-height, "title-strong-color": $title-strong-color, "title-strong-weight": $title-strong-weight, "subtitle-color": $subtitle-color, "subtitle-family": $subtitle-family, "subtitle-size": $subtitle-size, "subtitle-weight": $subtitle-weight, "subtitle-line-height": $subtitle-line-height, "subtitle-strong-color": $subtitle-strong-color, "subtitle-strong-weight": $subtitle-strong-weight, "subtitle-negative-margin": $subtitle-negative-margin)
+utilities.exportCSSVars($-css-vars-map)
+utilities.registerComponentCSSVars(('title', 'subtitle'), $-css-vars-map)
