.title,
.subtitle
  @extend %block
  word-break: break-word
  :is(em, span)
    font-weight: inherit
  sub
    font-size: var(--title-sub-size)
  sup
    font-size: var(--title-sup-size)
  .tag
    vertical-align: middle

.title
  color: var(--title-color)
  @if $title-family
    font-family: var(--title-family)
  font-size: var(--title-size)
  font-weight: var(--title-weight)
  line-height: var(--title-line-height)
  strong
    color: var(--title-strong-color)
    font-weight: var(--title-strong-weight)
  &:not(.is-spaced) + .subtitle
    +utilities.logical-y(margin, var(--subtitle-negative-margin), $bottom: false)

.subtitle
  color: var(--subtitle-color)
  @if $subtitle-family
    font-family: var(--subtitle-family)
  font-size: var(--subtitle-size)
  font-weight: var(--subtitle-weight)
  line-height: var(--subtitle-line-height)
  strong
    color: var(--subtitle-strong-color)
    font-weight: var(--subtitle-strong-weight)
  &:not(.is-spaced) + .title
    +utilities.logical-y(margin, var(--subtitle-negative-margin), $bottom: false)

// Sizes
.title,
.subtitle
  +utilities.sizes
