@use "sass:list"

@use "../utilities/derived-variables" as vars
@use "../utilities"

$panel-font-size: var(--size-normal) !default
$panel-margin: var(--block-spacing) !default
$panel-item-border: 1px solid var(--border-light) !default
$panel-radius: var(--radius-large) !default
$panel-shadow: var(--shadow) !default

$panel-heading-background-color: var(--border-light) !default
$panel-heading-color: var(--text-strong) !default
$panel-heading-line-height: 1.25 !default
$panel-heading-padding: 0.75em 1em !default
$panel-heading-radius: var(--radius) !default
$panel-heading-size: 1.25em !default
$panel-heading-weight: var(--weight-bold) !default

$panel-tabs-font-size: 0.875em !default
$panel-tab-border-bottom: 1px solid var(--border) !default
$panel-tab-active-border-bottom-color: var(--link-active-border) !default
$panel-tab-active-color: var(--link-active) !default

$panel-list-item-color: var(--text) !default
$panel-list-item-hover-color: var(--link) !default

$panel-block-color: var(--text-strong) !default
$panel-block-hover-background-color: var(--background) !default
$panel-block-active-border-inline-start-color: var(--link) !default
$panel-block-active-color: var(--link-active) !default
$panel-block-active-icon-color: var(--link) !default

$panel-icon-color: var(--text-light) !default
$panel-colors: vars.$colors !default

// Map Local variables
$-css-vars-map: ("panel-font-size": $panel-font-size, "panel-margin": $panel-margin, "panel-item-border": $panel-item-border, "panel-radius": $panel-radius, "panel-shadow": $panel-shadow, "panel-heading-background-color": $panel-heading-background-color, "panel-heading-color": $panel-heading-color, "panel-heading-line-height": $panel-heading-line-height, "panel-heading-padding": $panel-heading-padding, "panel-heading-size": $panel-heading-size, "panel-heading-weight": $panel-heading-weight, "panel-tabs-font-size": $panel-tabs-font-size, "panel-tab-border-bottom": $panel-tab-border-bottom, "panel-tab-active-border-bottom-color": $panel-tab-active-border-bottom-color, "panel-tab-active-color": $panel-tab-active-color, "panel-list-item-color": $panel-list-item-color, "panel-list-item-hover-color": $panel-list-item-hover-color, "panel-block-color": $panel-block-color, "panel-block-hover-background-color": $panel-block-hover-background-color, "panel-block-active-border-inline-start-color": $panel-block-active-border-inline-start-color, "panel-block-active-color": $panel-block-active-color, "panel-block-active-icon-color": $panel-block-active-icon-color, "panel-icon-color": $panel-icon-color)
+utilities.exportCSSVars($-css-vars-map)
+utilities.registerComponentCSSVars("panel", $-css-vars-map)
