.panel
  border-radius: var(--panel-radius)
  box-shadow: var(--panel-shadow)
  font-size: var(--panel-font-size)
  &:not(:last-child)
    +utilities.logical-y(margin, var(--panel-margin), $bottom: true)
  // Colors
  @each $name, $components in $panel-colors
    $color: list.nth($components, 1)
    $color-invert: list.nth($components, 2)
    &.is-#{$name}
      .panel-heading
        background-color: var(--#{$name})
        color: var(--#{$name}-invert)
      .panel-tabs a.is-active
        border-bottom-color: var(--#{$name})
      .panel-block.is-active .panel-icon
        color: var(--#{$name})

.panel-tabs,
.panel-block
  &:not(:last-child)
    border-bottom: var(--panel-item-border)

.panel-heading
  background-color: var(--panel-heading-background-color)
  border-radius: var(--panel-radius) var(--panel-radius) 0 0
  color: var(--panel-heading-color)
  font-size: var(--panel-heading-size)
  font-weight: var(--panel-heading-weight)
  line-height: var(--panel-heading-line-height)
  padding: var(--panel-heading-padding)

.panel-tabs
  align-items: flex-end
  display: flex
  font-size: var(--panel-tabs-font-size)
  justify-content: center
  a
    border-bottom: var(--panel-tab-border-bottom)
    +utilities.logical-y(margin, -1px, $bottom: true)
    padding: 0.5em
    // Modifiers
    &.is-active
      border-bottom-color: var(--panel-tab-active-border-bottom-color)
      color: var(--panel-tab-active-color)

.panel-list
  a
    color: var(--panel-list-item-color)
    &:hover
      color: var(--panel-list-item-hover-color)

.panel-block
  align-items: center
  color: var(--panel-block-color)
  display: flex
  justify-content: flex-start
  padding: 0.5em 0.75em
  input[type="checkbox"]
    +utilities.logical-x(margin, 0.75em, $right: true)
  & > .control
    flex-grow: 1
    flex-shrink: 1
    width: 100%
  &.is-wrapped
    flex-wrap: wrap
  &.is-active
    +utilities.logical-x(border, 1px solid var(--panel-block-active-border-inline-start-color), $right: false);
    color: var(--panel-block-active-color)
    .panel-icon
      color: var(--panel-block-active-icon-color)
  &:first-child
    +utilities.logical-border-radius(top, var(--panel-radius))
  &:last-child
    +utilities.logical-border-radius(bottom, var(--panel-radius))

.panel-block:is(a, label)
  cursor: pointer
  &:hover
    background-color: var(--panel-block-hover-background-color)

.panel-icon
  +utilities.fa(14px, 1em)
  color: var(--panel-icon-color)
  +utilities.logical-x(margin, 0.75em, $right: true)
  .fa
    font-size: inherit
    line-height: inherit
