@use "../utilities"

$footer-background-color: var(--scheme-main-bis) !default
$footer-color: false !default
$footer-padding: 3rem 1.5rem 6rem !default

// Map Local variables
$-css-vars-map: ("footer-background-color": $footer-background-color, "footer-color": $footer-color, "footer-padding": $footer-padding)
+utilities.exportCSSVars($-css-vars-map)
+utilities.registerComponentCSSVars("footer", $-css-vars-map)
// --footer-background-color: #{$footer-background-color}
// --footer-color: #{$footer-color}
// --footer-padding: #{$footer-padding}

.footer
  background-color: var(--footer-background-color)
  padding: var(--footer-padding)
  @if $footer-color
    color: var(--footer-color)
