@use "sass:list"

@use "../utilities/derived-variables" as vars
@use "../utilities"

$button-color: var(--text-strong) !default
$button-background-color: var(--scheme-main) !default
$button-family: false !default
$button-font-size: var(--size-normal) !default

$button-border-color: var(--border) !default
$button-border-width: var(--control-border-width) !default

$button-padding-vertical: calc(0.5em - var(--button-border-width)) !default
$button-padding-horizontal: 1em !default

$button-hover-color: var(--link-hover) !default
$button-hover-border-color: var(--link-hover-border) !default

$button-focus-color: var(--link-focus) !default
$button-focus-border-color: var(--link-focus-border) !default
$button-focus-box-shadow-size: 0 0 0 0.125em !default
$button-focus-box-shadow-color: rgba(var(--scheme-invert-rgb), 0.25) !default

$button-active-color: var(--link-active) !default
$button-active-border-color: var(--link-active-border) !default

$button-text-color: var(--text) !default
$button-text-decoration: underline !default
$button-text-hover-background-color: var(--background) !default
$button-text-hover-color: var(--text-strong) !default

$button-ghost-background: none !default
$button-ghost-border-color: transparent !default
$button-ghost-color: var(--link) !default
$button-ghost-decoration: none !default
$button-ghost-hover-color: var(--link) !default
$button-ghost-hover-decoration: underline !default

$button-disabled-background-color: var(--scheme-main) !default
$button-disabled-border-color: var(--border) !default
$button-disabled-shadow: none !default
$button-disabled-opacity: 0.5 !default

$button-static-color: var(--text-light) !default
$button-static-background-color: var(--scheme-main-ter) !default
$button-static-border-color: var(--border) !default

$button-colors: vars.$colors !default
$button-responsive-sizes: ("mobile": ("small": calc(var(--size-small) * 0.75), "normal": calc(var(--size-small) * 0.875), "medium": var(--size-small), "large": var(--size-normal)), "tablet-only": ("small": calc(var(--size-small) * 0.875), "normal": (var(--size-small)), "medium": var(--size-normal), "large": var(--size-medium))) !default

// The button sizes use mixins so they can be used at different breakpoints
=button-small
  --button-font-size: var(--size-small)
  &:not(.is-rounded)
    border-radius: var(--radius-small)
=button-normal
  --button-font-size: var(--size-normal)
=button-medium
  --button-font-size: var(--size-medium)
=button-large
  --button-font-size: var(--size-large)

// Map Local variables
$-css-vars-map: ('button-color': $button-color, 'button-background-color': $button-background-color, 'button-family': $button-family, 'button-font-size': $button-font-size, 'button-border-color': $button-border-color, 'button-border-width': $button-border-width, 'button-padding-vertical': $button-padding-vertical, 'button-padding-horizontal': $button-padding-horizontal, 'button-hover-color': $button-hover-color, 'button-hover-border-color': $button-hover-border-color, 'button-focus-color': $button-focus-color, 'button-focus-border-color': $button-focus-border-color, 'button-focus-box-shadow-size': $button-focus-box-shadow-size, 'button-focus-box-shadow-color': $button-focus-box-shadow-color, 'button-active-color': $button-active-color, 'button-active-border-color': $button-active-border-color, 'button-text-color': $button-text-color, 'button-text-decoration': $button-text-decoration, 'button-text-hover-background-color': $button-text-hover-background-color, 'button-text-hover-color': $button-text-hover-color, 'button-ghost-background': $button-ghost-background, 'button-ghost-border-color': $button-ghost-border-color, 'button-ghost-color': $button-ghost-color, 'button-ghost-decoration': $button-ghost-decoration, 'button-ghost-hover-color': $button-ghost-hover-color, 'button-ghost-hover-decoration': $button-ghost-hover-decoration, 'button-disabled-background-color': $button-disabled-background-color, 'button-disabled-border-color': $button-disabled-border-color, 'button-disabled-shadow': $button-disabled-shadow, 'button-disabled-opacity': $button-disabled-opacity, 'button-static-color': $button-static-color, 'button-static-background-color': $button-static-background-color, 'button-static-border-color': $button-static-border-color)
+utilities.exportCSSVars($-css-vars-map)
+utilities.registerComponentCSSVars("button", $-css-vars-map)
