.button
  @extend %control
  @extend %unselectable
  background-color: var(--button-background-color)
  transition: color calc(2 * var(--speed)) var(--easing)
  transition: background-color calc(2 * var(--speed)) var(--easing)
  border-color: var(--button-border-color)
  border-width: var(--button-border-width)
  color: var(--button-color)
  cursor: pointer
  @if $button-family
    font-family: var(--button-family)
  font-size: var(--button-font-size)
  justify-content: center
  +utilities.logical-y(padding, var(--button-padding-vertical))
  +utilities.logical-x(padding, var(--button-padding-horizontal))
  text-align: center
  white-space: nowrap
  strong
    color: inherit
  .icon
    &,
    &:is(.is-small, .is-medium, .is-large)
      height: 1.5em
      width: 1.5em
    &:first-child:not(:last-child)
      +utilities.logical-x(margin, calc(-0.5 * var(--button-padding-horizontal) - var(--button-border-width)), $right: false)
      +utilities.logical-x(margin, calc(var(--button-padding-horizontal) * 0.25), $right: true)
    &:last-child:not(:first-child)
      +utilities.logical-x(margin, calc(var(--button-padding-horizontal) * 0.25), $right: false)
      +utilities.logical-x(margin, calc(-0.5 * var(--button-padding-horizontal) - var(--button-border-width)), $right: true)
    &:first-child:last-child
      +utilities.logical-x(margin, calc(-0.5 * var(--button-padding-horizontal) - var(--button-border-width)))
  // States
  &:is(:hover, .is-hovered)
    border-color: var(--button-hover-border-color)
    color: var(--button-hover-color)
  &:is(:focus, .is-focused)
    border-color: var(--button-focus-border-color)
    color: var(--button-focus-color)
    &:not(:active)
      box-shadow: var(--button-focus-box-shadow-size) var(--button-focus-box-shadow-color)
  &:is(:active, .is-active)
    border-color: var(--button-active-border-color)
    color: var(--button-active-color)
  // Colors
  &.is-text
    background-color: transparent
    border-color: transparent
    color: var(--button-text-color)
    text-decoration: var(--button-text-decoration)
    &:is(:hover, .is-hovered, :focus, .is-focused)
      background-color: var(--white-ter)
      color: var(--text-strong)
    &:is(:active, .is-active)
      background-color: var(--grey-lightest)
      color: var(--text-strong)
    &[disabled],
    fieldset[disabled] &
      background-color: transparent
      border-color: transparent
      box-shadow: none
  &.is-ghost
    background: var(--button-ghost-background)
    border-color: var(--button-ghost-border-color)
    color: var(--button-ghost-color)
    text-decoration: var(--button-ghost-decoration)
    &:is(:hover, .is-hovered)
      color: var(--button-ghost-hover-color)
      text-decoration: var(--button-ghost-hover-decoration)
  @each $name, $pair in $button-colors
    $color: list.nth($pair, 1)
    $color-invert: list.nth($pair, 2)
    &.is-#{$name}
      background-color: var(--#{$name})
      border-color: transparent
      color: var(--#{$name}-invert)
      &:is(:hover, .is-hovered)
        background-color: #{utilities.bulmaVarDarken($name, 2.5%)}
        border-color: transparent
        color: var(--#{$name}-invert)
      &:is(:focus, .is-focused)
        border-color: transparent
        color: var(--#{$name}-invert)
        &:not(:active)
          box-shadow: var(--button-focus-box-shadow-size) #{utilities.bulmaVarOpacity($name, 0.25)}
      &:is(:active, .is-active)
        background-color: #{utilities.bulmaVarDarken($name, 5%)}
        border-color: transparent
        color: var(--#{$name}-invert)
      &[disabled],
      fieldset[disabled] &
        background-color: var(--#{$name})
        border-color: var(--#{$name})
        box-shadow: none
      &.is-inverted
        background-color: var(--#{$name}-invert)
        color: var(--#{$name})
        &:is(:hover, .is-hovered)
          background-color: var(--#{$name}-invert-ter) // utilities.bulmaDarken($color-invert, 5%)
        &[disabled],
        fieldset[disabled] &
          background-color: var(--#{$name}-invert)
          border-color: transparent
          box-shadow: none
          color: var(--#{$name})
        &.is-loading
          &::after
            border-color: transparent transparent var(--#{$name}-invert) var(--#{$name}-invert) !important
      &.is-loading
        &::after
          border-color: transparent transparent var(--#{$name}-invert) var(--#{$name}-invert) !important
      &.is-outlined
        background-color: transparent
        border-color: var(--#{$name})
        color: var(--#{$name})
        &:is(:hover, .is-hovered, :focus, .is-focused)
          background-color: var(--#{$name})
          border-color: var(--#{$name})
          color: var(--#{$name}-invert)
        &.is-loading
          &::after
            border-color: transparent transparent var(--#{$name}) var(--#{$name}) !important
          &:is(:hover, .is-hovered, :focus, .is-focused)
            &::after
              border-color: transparent transparent var(--#{$name}-invert) var(--#{$name}-invert) !important
        &[disabled],
        fieldset[disabled] &
          background-color: transparent
          border-color: var(--#{$name})
          box-shadow: none
          color: var(--#{$name})
      &.is-inverted.is-outlined
        background-color: transparent
        border-color: var(--#{$name}-invert)
        color: var(--#{$name}-invert)
        &:is(:hover, .is-hovered, :focus, .is-focused)
          background-color: var(--#{$name}-invert)
          color: var(--#{$name})
        &.is-loading
          &:is(:hover, .is-hovered, :focus, .is-focused)
            &::after
              border-color: transparent transparent var(--#{$name}) var(--#{$name}) !important
        &[disabled],
        fieldset[disabled] &
          background-color: transparent
          border-color: var(--#{$name}-invert)
          box-shadow: none
          color: var(--#{$name}-invert)
      // If light and dark colors are provided
      @if list.length($pair) >= 4
        $color-light: list.nth($pair, 3)
        $color-dark: list.nth($pair, 4)
        &.is-light
          background-color: var(--#{$name}-light)
          color: var(--#{$name}-dark)
          &:is(:hover, .is-hovered)
            background-color: #{utilities.bulmaVarLighteness($name, calc(var(--#{$name}-light-l) - 2.5%))}
            border-color: transparent
            color: var(--#{$name}-dark)
          &:is(:active, .is-active)
            background-color: #{utilities.bulmaVarLighteness($name, calc(var(--#{$name}-light-l) - 5%))}
            border-color: transparent
            color: var(--#{$name}-dark)
  // Sizes
  &.is-small
    +button-small
  &.is-normal
    +button-normal
  &.is-medium
    +button-medium
  &.is-large
    +button-large
  // Modifiers
  &[disabled],
  fieldset[disabled] &
    background-color: var(--button-disabled-background-color)
    border-color: var(--button-disabled-border-color)
    box-shadow: var(--button-disabled-shadow)
    opacity: var(--button-disabled-opacity)
  &.is-fullwidth
    display: flex
    width: 100%
  &.is-loading
    color: transparent !important
    pointer-events: none
    &::after
      @extend %loader
      +utilities.center(1em)
      position: absolute !important
  &.is-static
    background-color: var(--button-static-background-color)
    border-color: var(--button-static-border-color)
    color: var(--button-static-color)
    box-shadow: none
    pointer-events: none
  &.is-rounded
    border-radius: var(--radius-rounded)
    +utilities.logical-x(padding, calc(var(--button-padding-horizontal) + 0.25em))

.buttons
  align-items: center
  display: flex
  flex-wrap: wrap
  justify-content: flex-start
  .button
    +utilities.logical-y(margin, 0.5rem, $bottom: true)
    &:not(:last-child, .is-fullwidth)
      +utilities.logical-x(margin, 0.5rem, $right: true)
  &:last-child
    +utilities.logical-y(margin, -0.5rem, $bottom: true)
  &:not(:last-child)
    +utilities.logical-y(margin, 1rem, $bottom: true)
  // Sizes
  &.are-small
    .button:not(.is-normal, .is-medium, .is-large)
      +button-small
  &.are-medium
    .button:not(.is-small, .is-normal, .is-large)
      +button-medium
  &.are-large
    .button:not(.is-small, .is-normal, .is-medium)
      +button-large
  &.has-addons
    .button
      &:not(:first-child)
        +utilities.logical-border-radius(left, 0)
      &:not(:last-child)
        +utilities.logical-border-radius(right, 0)
        +utilities.logical-x(margin, -1px, $right: true)
      &:last-child
        +utilities.logical-x(margin, 0, $right: true)
      &:is(:hover, .is-hovered)
        z-index: 2
      &:is(:focus, .is-focused, :active, .is-active, .is-selected)
        z-index: 3
        &:hover
          z-index: 4
      &.is-expanded
        flex-grow: 1
        flex-shrink: 1
  &.is-centered
    justify-content: center
    &:not(.has-addons)
      .button:not(.is-fullwidth)
        +utilities.logical-x(margin, 0.25rem)
  &:is(.is-right, .is-end)
    justify-content: flex-end
    &:not(.has-addons)
      .button:not(.is-fullwidth)
        +utilities.logical-x(margin, 0.25rem)

@each $name, $sizes in $button-responsive-sizes
  +utilities.breakpoint($name)
    @each $size, $value in $sizes
      @if $size != "normal"
        .button.is-responsive.is-#{$size}
          font-size: $value
      @else
        .button.is-responsive,
        .button.is-responsive.is-normal
          font-size: $value
