.modal
  @extend %overlay
  align-items: center
  display: none
  flex-direction: column
  justify-content: center
  overflow: hidden
  position: fixed
  z-index: var(--modal-z)
  // Modifiers
  &.is-active
    display: flex

.modal-background
  @extend %overlay
  background-color: var(--modal-background-background-color)

.modal-content,
.modal-card
  margin: 0 var(--modal-content-margin-mobile)
  max-height: calc(100vh - var(--modal-content-spacing-mobile))
  overflow: auto
  position: relative
  width: 100%
  // Responsiveness
  +utilities.from($modal-breakpoint)
    margin: 0 auto
    max-height: calc(100vh - var(--modal-content-spacing-tablet))
    width: var(--modal-content-width)

.modal-close
  @extend %delete
  height: var(--modal-close-dimensions)
  position: fixed
  +utilities.logical-x(inset, var(--modal-close-right), $right: true)
  top: var(--modal-close-top)
  width: var(--modal-close-dimensions)

.modal-card
  display: flex
  flex-direction: column
  max-height: calc(100vh - var(--modal-card-spacing))
  overflow: hidden
  -ms-overflow-y: visible

.modal-card-head,
.modal-card-foot
  align-items: center
  background-color: var(--modal-card-head-background-color)
  display: flex
  flex-shrink: 0
  justify-content: flex-start
  padding: var(--modal-card-head-padding)
  position: relative

.modal-card-head
  border-bottom: var(--modal-card-head-border-bottom)
  +utilities.logical-border-radius(top, var(--modal-card-head-radius))

.modal-card-title
  color: var(--modal-card-title-color)
  flex-grow: 1
  flex-shrink: 0
  font-size: var(--modal-card-title-size)
  line-height: var(--modal-card-title-line-height)

.modal-card-foot
  +utilities.logical-border-radius(bottom, var(--modal-card-foot-radius))
  border-top: var(--modal-card-foot-border-top)
  .button
    &:not(:last-child)
      +utilities.logical-x(margin, 0.5em, $right: true)

.modal-card-body
  +utilities.overflow-touch
  background-color: var(--modal-card-body-background-color)
  flex-grow: 1
  flex-shrink: 1
  overflow: auto
  padding: var(--modal-card-body-padding)
