.navbar
  background-color: var(--navbar-background-color)
  min-height: var(--navbar-height)
  position: relative
  z-index: var(--navbar-z)
  @each $name, $pair in $navbar-colors
    $color: list.nth($pair, 1)
    $color-invert: list.nth($pair, 2)
    &.is-#{$name}
      --navbar-burger-color: var(--#{$name}-invert)
      background-color: var(--#{$name})
      color: var(--#{$name}-invert)
      .navbar-brand
        & > .navbar-item,
        .navbar-link
          color: var(--#{$name}-invert)
        & > a.navbar-item,
        .navbar-link
          &:is(:focus, :hover, .is-active)
            --navbar-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
            background-color: var(--navbar-item-hover-background-color)
            color: var(--#{$name}-invert)
        .navbar-link
          &::after
            border-color: var(--#{$name}-invert)
      +utilities.from($navbar-breakpoint)
        --navbar-dropdown-item-active-background-color: var(--#{$name})
        --navbar-dropdown-item-active-color: var(--#{$name}-invert)
        :is(.navbar-brand, .navbar-start, .navbar-end)
          & > .navbar-item,
          .navbar-link
            color: var(--#{$name}-invert)
          & > a.navbar-item,
          .navbar-link
            &:is(:focus, :hover, .is-active)
              --navbar-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
              background-color: var(--navbar-item-hover-background-color)
              color: var(--#{$name}-invert)
          .navbar-link
            &::after
              border-color: var(--#{$name}-invert)
        .navbar-item.has-dropdown
          &:is(:focus, :hover, .is-active)
            .navbar-link
              --navbar-dropdown-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
              background-color: var(--navbar-dropdown-item-hover-background-color)
              color: var(--#{$name}-invert)
        .navbar-dropdown
          a.navbar-item
            &.is-active
              background-color: var(--#{$name})
              color: var(--#{$name}-invert)
  & > .container
    align-items: stretch
    display: flex
    min-height: var(--navbar-height)
    width: 100%
  &.has-shadow
    box-shadow: var(--navbar-box-shadow-size) var(--navbar-box-shadow-color)
  &:is(.is-fixed-bottom, .is-fixed-top)
    +navbar-fixed
  &.is-fixed-bottom
    bottom: 0
    &.has-shadow
      box-shadow: var(--navbar-bottom-box-shadow-size) var(--navbar-box-shadow-color)
  &.is-fixed-top
    top: 0

html,
body
  &.has-navbar-fixed-top
    +utilities.logical-y(padding, var(--navbar-height), $bottom: false)
  &.has-navbar-fixed-bottom
    +utilities.logical-y(padding, var(--navbar-height), $bottom: true)

.navbar-brand,
.navbar-tabs
  align-items: stretch
  display: flex
  flex-shrink: 0
  min-height: var(--navbar-height)

.navbar-brand
  a.navbar-item
    &:is(:focus, :hover)
      background-color: transparent

.navbar-tabs
  +utilities.overflow-touch
  max-width: 100vw
  overflow-x: auto
  overflow-y: hidden

.navbar-burger
  @extend %reset
  color: var(--navbar-burger-color)
  +utilities.hamburger(var(--navbar-height))
  +utilities.logical-x(margin, auto, $right: false)

.navbar-menu
  display: none

.navbar-item,
.navbar-link
  color: var(--navbar-item-color)
  display: block
  line-height: 1.5
  padding: 0.5rem 0.75rem
  position: relative
  .icon
    &:only-child
      +utilities.logical-x(margin, -0.25rem)

a.navbar-item,
.navbar-link
  cursor: pointer
  &:is(:focus, :focus-within, :hover, .is-active)
    background-color: var(--navbar-item-hover-background-color)
    color: var(--navbar-item-hover-color)

.navbar-item
  flex-grow: 0
  flex-shrink: 0
  img
    max-height: var(--navbar-item-img-max-height)
  &.has-dropdown
    padding: 0
  &.is-expanded
    flex-grow: 1
    flex-shrink: 1
  &.is-tab
    border-bottom: 1px solid transparent
    min-height: var(--navbar-height)
    +utilities.logical-y(padding, calc(0.5rem - 1px), $bottom: true)
    &:is(:focus, :hover)
      background-color: var(--navbar-tab-hover-background-color)
      border-bottom-color: var(--navbar-tab-hover-border-bottom-color)
    &.is-active
      background-color: var(--navbar-tab-active-background-color)
      border-bottom-color: var(--navbar-tab-active-border-bottom-color)
      border-bottom-style: var(--navbar-tab-active-border-bottom-style)
      border-bottom-width: var(--navbar-tab-active-border-bottom-width)
      color: var(--navbar-tab-active-color)
      +utilities.logical-y(padding, calc(0.5rem - var(--navbar-tab-active-border-bottom-width)), $bottom: true)

.navbar-content
  flex-grow: 1
  flex-shrink: 1

.navbar-link:not(.is-arrowless)
  +utilities.logical-x(padding, 2.5em, $right: true)
  &::after
    @extend %arrow
    border-color: var(--navbar-dropdown-arrow)
    +utilities.logical-y(margin, -0.375em, $bottom: false)
    +utilities.logical-x(inset, 1.125em, $right: true)

.navbar-dropdown
  font-size: 0.875rem
  +utilities.logical-y(padding, 0.5rem)
  .navbar-item
    +utilities.logical-x(padding, 1.5rem)

.navbar-divider
  background-color: var(--navbar-divider-background-color)
  border: none
  display: none
  height: var(--navbar-divider-height)
  margin: 0.5rem 0

+utilities.until($navbar-breakpoint)
  .navbar > .container
    display: block
  :is(.navbar-brand, .navbar-tabs)
    .navbar-item
      align-items: center
      display: flex
  .navbar-link:not(.is-arrowless)
    &::after
      display: none
  .navbar-menu
    background-color: var(--navbar-background-color)
    box-shadow: 0 8px 16px var(--navbar-shadow-color)
    padding: 0.5rem 0
    &.is-active
      display: block
  // Fixed navbar
  .navbar
    &:is(.is-fixed-bottom-touch, .is-fixed-top-touch)
      +navbar-fixed
    &.is-fixed-bottom-touch
      bottom: 0
      &.has-shadow
        box-shadow: 0 -2px 3px var(--navbar-shadow-color)
    &.is-fixed-top-touch
      top: 0
    &:is(.is-fixed-top, .is-fixed-top-touch)
      .navbar-menu
        +utilities.overflow-touch
        max-height: calc(100vh - var(--navbar-height))
        overflow: auto
  html,
  body
    &.has-navbar-fixed-top-touch
      +utilities.logical-y(padding, var(--navbar-height), $bottom: false)
    &.has-navbar-fixed-bottom-touch
      +utilities.logical-y(padding, var(--navbar-height), $bottom: true)

+utilities.from($navbar-breakpoint)
  .navbar,
  .navbar-menu,
  .navbar-start,
  .navbar-end
    align-items: stretch
    display: flex
  .navbar
    min-height: var(--navbar-height)
    &.is-spaced
      padding: var(--navbar-padding-vertical) var(--navbar-padding-horizontal)
      :is(.navbar-start, .navbar-end)
        align-items: center
      :is(a.navbar-item, .navbar-link)
        border-radius: var(--radius)
    &.is-transparent
      :is(a.navbar-item, .navbar-link)
        &:is(:focus, :hover, .is-active)
          background-color: transparent !important
      .navbar-item.has-dropdown
        &:is(.is-active, .is-hoverable:is(:focus, :focus-within, :hover))
          .navbar-link
            background-color: transparent !important
      .navbar-dropdown
        a.navbar-item
          &:is(:focus, :hover)
            background-color: var(--navbar-dropdown-item-hover-background-color)
            color: var(--navbar-dropdown-item-hover-color)
          &.is-active
            background-color: var(--navbar-dropdown-item-active-background-color)
            color: var(--navbar-dropdown-item-active-color)
  .navbar-burger
    display: none
  :is(.navbar-item, .navbar-link)
    align-items: center
    display: flex
  .navbar-item
    &.has-dropdown
      align-items: stretch
    &.has-dropdown-up
      .navbar-link::after
        transform: rotate(135deg) translate(0.25em, -0.25em)
      .navbar-dropdown
        border-radius: var(--navbar-dropdown-radius) var(--navbar-dropdown-radius) 0 0
        border-block: none var(--navbar-dropdown-border-top)
        bottom: 100%
        box-shadow: 0 -8px 8px var(--navbar-shadow-color)
        top: auto
    &:is(.is-active, .is-hoverable:is(:focus, :focus-within, :hover))
      .navbar-dropdown
        display: block
        .navbar.is-spaced &,
        &.is-boxed
          opacity: 1
          pointer-events: auto
          transform: translateY(0)
  .navbar-menu
    flex-grow: 1
    flex-shrink: 0
  .navbar-start
    justify-content: flex-start
    +utilities.logical-x(margin, auto, $right: true)
  .navbar-end
    justify-content: flex-end
    +utilities.logical-x(margin, auto, $right: false)
  .navbar-dropdown
    background-color: var(--navbar-dropdown-background-color)
    +utilities.logical-border-radius(bottom, var(--navbar-dropdown-radius))
    border-top: var(--navbar-dropdown-border-top)
    box-shadow: 0 8px 8px var(--navbar-shadow-color)
    display: none
    font-size: 0.875rem
    +utilities.logical-x(inset, 0, $right: false)
    min-width: 100%
    position: absolute
    top: 100%
    z-index: var(--navbar-dropdown-z)
    .navbar-item
      padding: var(--navbar-item-padding)
      white-space: nowrap
    a.navbar-item
      +utilities.logical-x(padding, 3rem, $right: true)
      &:is(:focus, :hover)
        background-color: var(--navbar-dropdown-item-hover-background-color)
        color: var(--navbar-dropdown-item-hover-color)
      &.is-active
        background-color: var(--navbar-dropdown-item-active-background-color) !important
        color: var(--navbar-dropdown-item-active-color)
    .navbar.is-spaced &,
    &.is-boxed
      border-radius: var(--navbar-dropdown-boxed-radius)
      border-top: none
      box-shadow: var(--navbar-dropdown-boxed-shadow)
      display: block
      opacity: 0
      pointer-events: none
      top: calc(100% + var(--navbar-dropdown-offset))
      transform: translateY(-5px)
      transition-duration: var(--speed)
      transition-property: opacity, transform
    &:is(.is-right, .is-end)
      +utilities.logical-x(inset, auto 0)
  .navbar-divider
    display: block
  .navbar > .container,
  .container > .navbar
    .navbar-brand
      +utilities.logical-x(margin, -0.75rem, $right: false)
    .navbar-menu
      +utilities.logical-x(margin, -0.75rem, $right: true)
  // Fixed navbar
  .navbar
    &:is(.is-fixed-bottom-desktop, .is-fixed-top-desktop)
      +navbar-fixed
    &.is-fixed-bottom-desktop
      bottom: 0
      &.has-shadow
        box-shadow: 0 -2px 3px var(--navbar-shadow-color)
    &.is-fixed-top-desktop
      top: 0
  :is(html, body)
    &.has-navbar-fixed-top-desktop
      +utilities.logical-y(padding, var(--navbar-height), $bottom: false)
    &.has-navbar-fixed-bottom-desktop
      +utilities.logical-y(padding, var(--navbar-height), $bottom: true)
    &.has-spaced-navbar-fixed-top
      +utilities.logical-y(padding, calc(var(--navbar-height) + calc(var(--navbar-padding-vertical) * 2)), $bottom: false)
    &.has-spaced-navbar-fixed-bottom
      +utilities.logical-y(padding, calc(var(--navbar-height) + calc(var(--navbar-padding-vertical) * 2)), $bottom: true)
  // Hover/Active states
  :is(a.navbar-item, .navbar-link)
    &.is-active
      color: var(--navbar-item-active-color)
    &.is-active:not(:focus, :hover)
      background-color: var(--navbar-item-active-background-color)
  .navbar-item.has-dropdown
    &:is(:focus, :hover, .is-active)
      .navbar-link
        background-color: var(--navbar-item-hover-background-color)

// Combination

.hero
  &.is-fullheight-with-navbar
    min-height: calc(100vh - var(--navbar-height))
